angular.module("app").controller('GeneralController',
    ['$scope', '$location', '$interval', 'AuthenticationService', 'DateService', 'SynchroResource',
      'settings', 'lastSyncReport', 'syncStatus',
      function($scope, $location, $interval, AuthenticationService, DateService, SynchroResource,
               settings, lastSyncReport, syncStatus)
{
  $scope.autoSyncStatus = settings && settings.autoSyncEnabled ? 'ON' : 'OFF';
  $scope.lastTime = DateService.fromTimestampToString(lastSyncReport && lastSyncReport.endTime);
  $scope.lastStatus = lastSyncReport && lastSyncReport.status;
  $scope.syncStatus = transformSyncStatus(syncStatus);
  $scope.isRunning = $scope.syncStatus.syncRunning;
  $scope.isWaiting = false;
  $scope.settings = settings;

  $scope.sync = function() {
    SynchroResource.execute();
    $scope.isWaiting = true;
  };

  $scope.logout = function () {
    $scope.isCollapsed = true;
    AuthenticationService.logout();
    $location.path('/login');
  };

  $interval(function() {
    SynchroResource.getSyncStatus({}, function(syncStatus) {
      $scope.syncStatus = transformSyncStatus(syncStatus);
      $scope.isRunning = $scope.syncStatus.syncRunning;
      // turn off the waiting flag if task is running now
      if($scope.isWaiting && $scope.isRunning) {
        $scope.isWaiting = false;
      }
    });

    SynchroResource.getLastSyncReport({}, function(lastSyncReport) {
      $scope.lastTime = DateService.fromTimestampToString(lastSyncReport && lastSyncReport.endTime);
      $scope.lastStatus = lastSyncReport && lastSyncReport.status;
    });
  }, 1000);

  function transformSyncStatus(syncStatus) {
    syncStatus.syncRunning = (syncStatus.syncRunning === "true");

    if(syncStatus.syncRunning) {

      switch (syncStatus.syncPhase) {
        case "INITIALIZING":
          syncStatus.syncPhase = "Initializing";
          delete syncStatus.syncResource;
          delete syncStatus.resourcesSynced;
          break;
        case "SYNC_DATA_UPDATING":
          syncStatus.syncPhase = "Synchronization data updating";
          syncStatus.syncResource = syncStatus.syncResource.toLowerCase();
          syncStatus.resourcesSynced = parseInt(syncStatus.resourcesSynced);
          break;
        case "SYNC_MARKETO_SAGE":
          syncStatus.syncPhase = "Synchronization Marketo -> Sage";
          syncStatus.syncResource = syncStatus.syncResource.toLowerCase();
          syncStatus.resourcesSynced = parseInt(syncStatus.resourcesSynced);
          break;
        case "SYNC_SAGE_MARKETO":
          syncStatus.syncPhase = "Synchronization SAGE -> Marketo";
          syncStatus.syncResource = syncStatus.syncResource.toLowerCase();
          syncStatus.resourcesSynced = parseInt(syncStatus.resourcesSynced);
          break;
      }
    } else {
      delete syncStatus.syncPhase;
      delete syncStatus.syncResource;
      delete syncStatus.resourcesSynced;
    }

    return syncStatus;
  }
}]);