angular.module("app").controller('SettingsController',
['$scope', '$location', 'DateService', 'SettingsResource', 'settings',
function($scope, $location, DateService, SettingsResource, settings) {

  // todo: for testing purposes
  if(!settings.marketoCliendId) {
    settings.marketoCliendId = "57507ab8-aa26-43f3-ab88-5221d237730c";
    settings.marketoClientSecret = "MKXrWSQJSevyiNbk4YWd2zEJGHlceWlH";
    settings.marketoServerProtocol = "https";
    settings.marketoServerHost = "275-OTA-175.mktorest.com";
    settings.marketoLeadsListId = "1060";
    settings.sageLogin = "lfabric";
    settings.sagePassword = "Marketo2015";
    settings.sageServerProtocol = "http";
    settings.sageServerHost = "192.168.242.201";
    settings.sageInstallName = "crm";
  }
  settings.marketoLeadsListId = settings.marketoLeadsListId && parseInt(settings.marketoLeadsListId);
  $scope.settings = settings;



  $scope.save = function() {
    var settings = angular.copy($scope.settings);
    settings.schedule.startTime = DateService.fromDateToTimestamp(settings.schedule.startTime);
    settings.schedule.period = Math.floor($scope.settings.schedule.period * (60*1000));

    SettingsResource.update(settings).$promise.then(function(response) {
      $scope.settings = response;
      $scope.settings.schedule.startTime = DateService.fromTimestampToDate($scope.settings.schedule.startTime);
      $scope.settings.schedule.period = Math.floor($scope.settings.schedule.period / (60*1000));
      $scope.status = 'Saved';
    }, function() {
      $scope.status = 'Failed';
    });
  };

  $scope.cancel = function() {
    $location.path('/general');
  };

  $scope.setNow = function() {
    var date = new Date();
    date.setMilliseconds(0);
    date.setSeconds(0);
    $scope.settings.schedule.startTime = date;
  };

  $scope.settings.schedule.startTime = DateService.fromTimestampToDate($scope.settings.schedule.startTime);
  $scope.settings.schedule.period =  Math.floor($scope.settings.schedule.period / (1000*60));

}]);